.ctext{
    font-weight: 700;
    font-size: 40px;
    margin-bottom: 20px;
    color: #001c55;
    margin-top: 70px;
    margin-bottom: 30px;
}

.cflex1{
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #001c55;
}

.cflex2{
    width: 150px;
    margin: auto;
    margin-top: 30px;
    gap: 15px;
    font-size: 20px;
    margin-bottom: 30px;
}

.cimage{
    width: 800px;
    height: 500px;
}

.ctext2{
    font-size: 20px;
    margin-top: 15px;
    font-weight: 500;
    font-family: cursive;
}

.cbox{
    width: 650px;
}

@media (min-width: 360px) and (max-width: 480px) {
    .cimage{
        width: 400px; 
        height: 100px;
    }

    .ctext1{
        font-size: 20px;
        font-weight: 500;
    }


    .ctext2{
        font-size: 10px;
    }

    .cflex2{
        width: 150px;
        margin: auto;
        margin-top: 15px;
        gap: 15px;
        font-size: 15px;
        margin-bottom: 15px;
    }
    
  }


  @media (min-width: 480px) and (max-width: 768px) {
    .cimage{
        width: 300px;
        height: 200px;
    }


    .ctext{
        font-weight: 700;
        font-size: 30px;
    }


}  