.ptext{
    font-weight: 700;
    font-size: 40px;
    margin-bottom: 20px;
    color: #001c55;
    margin-top: 70px;
    margin-bottom: 50px;
}

.pflex1{
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
    color: #001c55;
}

.pimage1{
    width: 700px;
}

.ptext1{
    font-size: 20px;
    font-weight: 500;
    font-family: cursive;
}

.pgrid{
    width: 70%;
    margin: auto;
    gap: 50px;
    margin-top: 30px;
    grid-template-columns: repeat(3,1fr);
    color: #001c55;
}

.pbox{
    border: 1px solid rgb(216, 214, 249);
    padding: 10px;
    border-radius: 10px;
    background-color: #eef4ff;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
    font-family: cursive;
}

.pbox:hover{
    border: 1px solid #001c55;
}

.pimage2{
    width: 350px;
    height: 150px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border: 0.5px solid rgb(205, 202, 243);
}

